import React, { useState, useEffect } from "react";
import {
  AdaptivityProvider,
  ConfigProvider,
  useAdaptivity,
  AppRoot,
  SplitLayout,
  SplitCol,
  ViewWidth,
  View,
  Panel,
  PanelHeader,
  Header,
  Group,
  SimpleCell,
  ScreenSpinner,
} from "@vkontakte/vkui";

import Intro from "./panels/Intro";
import Home from "./panels/Home";

import Deploy from "./panels/Deploy";

import Finish from "./panels/Finish";

const App = () => {
  const { viewWidth } = useAdaptivity();
  const [activePanel, setActivePanel] = useState("intro");
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);

  useEffect(() => {
    setPopout(null);
  }, []);

  const go = (panel) => {
    // ads();
    setActivePanel(panel);
  };

  return (
    <ConfigProvider>
      <AdaptivityProvider>
        <AppRoot>
          <View activePanel={activePanel} popout={popout}>
            <Intro id="intro" fetchedUser={"fetchedUser"} go={go} />
            <Home id="home" fetchedUser={"fetchedUser"} go={go} />
            <Deploy id="deploy" fetchedUser={"fetchedUser"} go={go} />
            <Finish id="finish" fetchedUser={"fetchedUser"} go={go} />
            {/* <Home id='home' fetchedUser={fetchedUser} go={go}/>
					<Deploy id='deploy' fetchedUser={fetchedUser} go={go}/>
					<Finish id='finish' fetchedUser={fetchedUser} go={go}/> */}
          </View>
        </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  );
};

export default App;
