import React from "react";
import { Panel, Button, Div } from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import "./style.css";
import { Icon24Reorder } from "@vkontakte/icons";
import { Icon56NotePenOutline } from "@vkontakte/icons";

async function go_story(id) {
  let b_id = 600000000 - id;
  b_id = Math.floor(b_id / 10000000);
  b_id = Math.floor((b_id * 7) / 6);
  b_id = b_id % 50;
  console.log(b_id);

  let a = await bridge.send("VKWebAppCallAPIMethod", {
    method: "photos.get",
    params: {
      owner_id: -186518584,
      album_id: 264988399,
      access_token: localStorage.getItem("token"),
      v: "5.131",
    },
  });
  // console.log(a.response)

  await bridge.send("VKWebAppShowStoryBox", {
    background_type: "image",
    url: a.response.items[b_id].sizes[9].url,
    attachment: {
      text: "learn_more",
      type: "url",
      url: "https://vk.com/app7910961",
    },
  });
}

const Finish = ({ id, fetchedUser }) => (
  <Panel id={id}>
    <Div className="subscrib">
      <Div className="button_f">
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          before={<Icon56NotePenOutline />}
          onClick={() => {
            go_story(fetchedUser.id);
          }}
        >
          Посмотреть результат
        </Button>
      </Div>
      <Div className="button_f">
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          before={<Icon24Reorder />}
          onClick={(_) => {
            bridge.send("VKWebAppOpenApp", { app_id: 7761604 });
          }}
        >
          Узнать дату смерти
        </Button>
      </Div>
      <Div className="button_f">
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          before={<Icon24Reorder />}
          onClick={(_) => {
            bridge.send("VKWebAppOpenApp", { app_id: 7948874 });
          }}
        >
          Узнай через сколько ты потеряешь девственность
        </Button>
      </Div>
      <Div className="button_f">
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          before={<Icon24Reorder />}
          onClick={(_) => {
            bridge.send("VKWebAppOpenApp", { app_id: 7891207 });
          }}
        >
          Размер твоего банана
        </Button>
      </Div>
      <Div className="button_f">
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          before={<Icon24Reorder />}
          onClick={(_) => {
            bridge.send("VKWebAppOpenApp", { app_id: 7552966 });
          }}
        >
          Сколько стоит ваша жизнь
        </Button>
      </Div>
    </Div>
  </Panel>
);

export default Finish;
