import React from "react";
import {
  Panel,
  PanelHeader,
  Header,
  Button,
  Group,
  Cell,
  Div,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import "./style.css";

function loading() {
  setTimeout(
    () =>
      (document.getElementById("child").innerText = "получаем ваши данные..."),
    1500
  );
  setTimeout(
    () =>
      (document.getElementById("child").innerText = "анализируем данные..."),
    2700
  );
  setTimeout(
    () => (document.getElementById("child").innerText = "опрашиваем друзей..."),
    5000
  );
  setTimeout(
    () =>
      (document.getElementById("child").innerText =
        "анализируем ваш профиль.."),
    6000
  );
  setTimeout(
    () =>
      (document.getElementById("child").innerText = "почти готово,подождите.."),
    7300
  );
  setTimeout(
    () => (document.getElementById("child").innerText = "еще чуть-чуть..."),
    8300
  );
  setTimeout(() => {
    var a = document.getElementById("button");
    a.style.display = "inline";

    document.getElementById("child").style.display = "none";
  }, 9000);
}

const Deploy = ({ id, go, fetchedUser }) => (
  <Panel id={id} className="Panel">
    <Div className="subscrib">
      <Div className="child">
        <img
          src={"https://svgsilh.com/svg/1293387.svg"}
          height={150}
          width={150}
        />
      </Div>
      <Div
        id="child"
        className="child"
        style={{
          fontWeight: "bolder",
          fontSize: "2.5em",
        }}
      >
        собираем информацию...
      </Div>
      <Div id="button">
        <h1>Анализ завершён</h1>
        <h5>Самое время узнать, сколько времени отнял у тебя ВК!</h5>
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          onClick={() => {
            go("finish");
          }}
        >
          Посмотреть результат
        </Button>
      </Div>
      <Cell onClick={loading(go)} />
    </Div>
  </Panel>
);

export default Deploy;
