import React from "react";

import { Panel, Div, Button } from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import "./style.css";

function checkSubscribe(go) {
  go("home");
  //   try {
  //     bridge.send("VKWebAppJoinGroup", { group_id: 203915434 });
  //     go("home");
  //   } catch (e) {
  //     console.log(e);
  //   }
}

function Intro({ id, go }) {
  return (
    <Panel id={id} className="Panel">
      <Div className="subscribe">
        <Div className="child">
          <h1>Привет!</h1>
          <h4>
            Наше приложение не является официальным и не может предоставить
            точную статистику*
          </h4>
          <h4>
            *Приблизительная статистика высчитывается по формулам разработанным
            специалистами.
          </h4>
          <Button
            stretched
            size="l"
            mode="Overlay Primary"
            id="subs"
            onClick={() => checkSubscribe(go)}
          >
            Oк
          </Button>
        </Div>
      </Div>
    </Panel>
  );
}

export default Intro;
